import { styled, Button } from "@mui/material";

const SubmitButton = styled(Button)({
  display: "flex",
  gap: ".7em",
  color: "#000",
  background: "#fff",
  marginRight: ".7em",
  borderRadius: "5px",
  paddingBlock: ".5em",
  minWidth: "90px",
  flexGrow: 0,
  textTransform: "capitalize",
  border: "1px solid #000",
  "&:hover": {
    color: "#000",
    background: "#f1f3f4",
    border: "1px solid #000",
  },
});

export default SubmitButton;
