import { useState } from "react";
import { Stack, IconButton } from "@mui/material";
import ClearIcon from "@mui/icons-material/Clear";

import InputField from "../InputField";

const SocialItem = ({ enteredUrl, id }) => {
  const [editedUrl, setEditedUrl] = useState(enteredUrl);

  const handleChange = (e) => {
    setEditedUrl(e.target.value);
  };

  return (
    <Stack
      alignItems="center"
      justifyContent="flex-start"
      sx={{ minWidth: "100%" }}
      direction="row"
      spacing={1}
    >
      <InputField
        id="socials"
        onChange={handleChange}
        value={editedUrl}
      />
      <IconButton sx={{ color: "#000", maxWidth: "30px", border: "1px solid transparent", borderRadius: "5px" }}>
        <ClearIcon fontSize="35px" />
      </IconButton>
    </Stack>
  );
};

export default SocialItem;
