import { useContext } from "react";
import { Container, Box, styled, IconButton } from "@mui/material";
import GlobalContext from "../../context/GlobalContextProvider";
import { Edit as EditIcon } from "@mui/icons-material";

const CoverImageContainer = styled(Box)({
  padding: 0,
  "& img": {
    width: "100%",
    height: "200px",
    borderRadius: "20px",
    objectFit: "cover",
  },
});

const ProfileImageContainer = styled(Box)(({ isdesktop }) => ({
  padding: 0,
  width: "max-content",
  position: "absolute",
  bottom: isdesktop ? "-110px" : "-90px",
  left: isdesktop ? "50px" : "50%",
  transform: isdesktop ? "translate(0)" : "translate(-50%, 0)",
}));

const CoverEditBtn = styled(IconButton)({
  position: "absolute",
  width: "60px",
  aspectRatio: "1",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  background: "var(--clr-main)",
});

const ProfileEditBtn = styled(IconButton)({
  position: "absolute",
  bottom: "-15px",
  right: "-15px",
  transform: "translate(-50%, -50%)",
  background: "var(--clr-main)",
});

const ProfileHeader = ({ isdesktop }) => {
  return (
    <Container maxWidth="lg" sx={{ position: "relative", padding: 0, zIndex: 100 }}>
      <CoverImageContainer>
        <Box sx={{ position: "relative" }}>
          <img
            src={isdesktop ? "images/bg-cover.png" : "images/bg-cover-mob.png"}
          />
          {isdesktop ? (
            <CoverEditBtn>
              <EditIcon style={{ color: "#000" }} />
            </CoverEditBtn>
          ) : null}
        </Box>
      </CoverImageContainer>
      <ProfileImageContainer isdesktop={isdesktop ? 1 : 0}>
        <Box sx={{ position: "relative" }}>
          <img src="images/profile-img.png" />
          <ProfileEditBtn>
            <EditIcon style={{ color: "#000" }} fontSize="small" />
          </ProfileEditBtn>
        </Box>
      </ProfileImageContainer>
    </Container>
  );
};

export default ProfileHeader;
