import { useContext } from "react";
import { Box, Stack, styled, FormGroup, TextField } from "@mui/material";

import InputField from "../InputField";
import LabelField from "../LabelField";
import GlobalContext from "../../../../context/GlobalContextProvider";

const InputContainer = styled(Box)(({ istab }) => ({
  width: "100%",
  display: "flex",
  flexDirection: istab ? "row" : "column",
  gap: "1em",
}));

const GeneralDetails = () => {
  const { istab, isdesktop } = useContext(GlobalContext);

  return (
    <Stack spacing={{xs: 3, md: 2}}>
      <Stack alignItems={{xs: "stretch", md: "center"}} direction={{ xs: "column", md: "row" }} spacing={2}>
        <LabelField sx={{width: "90px"}} htmlFor="name">Name</LabelField>
        <InputContainer isdesktop={isdesktop ? 1 : 0} istab={istab ? 1 : 0}>
          <InputField id="name" placeholder="Mohd" />
          <InputField placeholder="Omar" />
        </InputContainer>
      </Stack>
      <Stack alignItems={{xs: "stretch", md: "center"}} direction={{ xs: "column", md: "row" }} spacing={2}>
        <LabelField sx={{width: "90px"}} htmlFor="email">Email</LabelField>
        <InputField id="email" placeholder="gmatrixwebworks@gmail.com" />
      </Stack>
      <Stack alignItems={{xs: "stretch", md: "baseline"}} direction={{ xs: "column", md: "row" }} spacing={2}>
        <LabelField sx={{width: "90px"}} htmlFor="bio">Bio</LabelField>
        <TextField
        fullWidth
          multiline
          rows={4}
          id="bio"
          placeholder="I am a freelance designer based in India"
        />
      </Stack>
      <Stack alignItems={{xs: "stretch", md: "center"}} direction={{ xs: "column", md: "row" }} spacing={2}>
        <LabelField sx={{width: "90px"}} htmlFor="phone">Ph No.</LabelField>
        <InputField id="phone" placeholder="+91 9911058036" />
      </Stack>
    </Stack>
  );
};

export default GeneralDetails;
