import { Box, Container, Typography, Button, styled } from "@mui/material";
import FormCTA from "./FormCTA";

const MainContainer = styled(Container)(({ isdesktop }) => ({
  display: "flex",
  justifyContent: "space-between",
  flexDirection: isdesktop ? "row" : "column",
  alignItems: "center",
  textAlign: isdesktop ? "left" : "center",
  gap: "2em",
  padding: 0, 
  marginTop: isdesktop ? 0 : "7em" ,
  width: isdesktop ? "80%" : "100%",
  maxWidth: "1000px",
  position: isdesktop ? "absolute" : "static",
  right: "0",
  top: "15px"
}));

const FormHeader = ({ isdesktop }) => {

  return (
    <MainContainer isdesktop={isdesktop ? 1 : 0}>
      <Box>
        <Typography
          sx={{ fontWeight: "600", marginBottom: "7px" }}
          variant="h4"
        >
          Profile
        </Typography>
        <Typography sx={{ color: "#727272" }} variant="subtitle2">
          Update your photo and personal details.
        </Typography>
      </Box>
      <FormCTA />
    </MainContainer>
  );
};

export default FormHeader;
