import "./AppBar.css";
import Dropdown from "./AppBarComponents/Dropdown";
import MenuButton from "./AppBarComponents/MenuButton";

const AppBar = () => {
  if (window.matchMedia("(max-width: 767px)").matches) {
    document.querySelectorAll(".navbar li").forEach((link) => {
      link.addEventListener("click", () => {
        document.querySelector(".navbar").classList.remove("open");
        document.querySelector("body").classList.remove("fixed-position");
        document.querySelectorAll(".menu-btn span").forEach((span) => {
          span.classList.remove("open");
        });
      });
    });
  }

  return (
    <div className="header-container">
      <header className="header">
        <img src="images/logo.png" />
        <MenuButton />
        <nav className="navbar" data-visible="false">
          <ul>
            <li>
              <a className="join-link" href="#join">
                About
              </a>
            </li>
            <li className="flex-col">
              <span>LIVE</span>
              <a className="login-link" href="#class">
                Masterclass
              </a>
            </li>
            <li className="flex-col">
              <span>FREE</span>
              <a>
                <Dropdown />
              </a>
            </li>
            <li className="flex-col">
              <span>HOT</span>
              <a className="login-link" href="#solvedq">
                Solved Questions
              </a>
            </li>
            <li>
              <a className="" href="#doubt">
                Doubt Space
              </a>
            </li>
          </ul>
          <a className="flex profileCTA-container">
            <img width="30px" src="images/profileCTA.png" alt="logo" />
            <p>GmatriX</p>
          </a>
        </nav>
      </header>
    </div>
  );
};

export default AppBar;
