import { useState, useContext } from "react";
import { Box, Stack, IconButton } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";

import InputField from "../InputField";
import LabelField from "../LabelField";
import SocialItem from "./SocialItem";
import SubmitButton from "../SubmitButton";
import { v4 as uuid } from "uuid";
import GlobalContext from "../../../../context/GlobalContextProvider";

const Social = () => {
  const [enteredUrl, setEnteredUrl] = useState("");
  const [inputList, setInputList] = useState([]);
  const { istab } = useContext(GlobalContext);

  const handleChange = (e) => {
    setEnteredUrl(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setInputList((prev) => {
      return [...prev, { url: enteredUrl, id: uuid() }];
    });
    setEnteredUrl("");
  };

  return (
    <Box component="form" onSubmit={handleSubmit}>
      <Stack spacing={{xs:1, md: 1.4}}>
      <LabelField htmlFor="socials">
        Social Links
      </LabelField>
        <Stack spacing={{xs: 2, md: 3}}>
          {inputList.map((input) => (
            <SocialItem
              setInputList={setInputList}
              key={input.id}
              id={input.id}
              enteredUrl={input.url}
            />
          ))}
        </Stack>
        <Stack
          alignItems="center"
          sx={{ width: "100%" }}
          direction="row"
          spacing={1}
        >
          <InputField
            type="url"
            id="socials"
            placeholder="instagram.com/gmatrixwebworks"
            onChange={handleChange}
            value={enteredUrl}
          />
          {istab ? (
            <SubmitButton type="submit">
              <AddIcon fontSize="small" />
              Add
            </SubmitButton>
          ) : (
            <IconButton
              sx={{ border: "1px solid #000", padding: ".2em", borderRadius: "4px" }}
              type="submit"
            >
              <AddIcon sx={{ color: "#000" }} />
            </IconButton>
          )}
        </Stack>
      </Stack>
    </Box>
  );
};

export default Social;
