import { Button, styled, Box, Stack } from "@mui/material";

const CancelButton = styled(Button)({
  color: "#000",
  background: "#fff",
  marginRight: ".7em",
  borderRadius: "5px",
  border: "1px solid #000",
  padding: ".6em 2em",
  textTransform: "capitalize",
  "&:hover": {
    color: "#000",
    background: "#f1f3f4",
    border: "1px solid #000",
  },
});

const SaveButton = styled(Button)({
  color: "#fff",
  background: "#000",
  borderRadius: "5px",
  border: "1px solid #000",
  padding: ".6em 2em",
  textTransform: "capitalize",
  "&:hover": {
    color: "#fff",
    background: "#000",
    border: "1px solid #000",
  },
});

const FormCTA = () => {
  return (
    <Stack direction="row">
      <CancelButton variant="outlined">Cancel</CancelButton>
      <SaveButton variant="outlined">Save</SaveButton>
    </Stack>
  );
};

export default FormCTA;
