import { useState } from "react";
import { Stack, Box, IconButton } from "@mui/material";
import ClearIcon from "@mui/icons-material/Clear";

import MyDatePicker from "../MyDatePicker";
import InputField from "../InputField";

const WorkItem = ({ enteredWork, id, fromYear, toYear }) => {
  const [editedWork, setEditedWork] = useState(enteredWork);
  const [editedFromYear, setEditedFromYear] = useState(fromYear);
  const [editedToYear, setEditedToYear] = useState(toYear);

  const handleChange = (e) => {
    setEditedWork(e.target.value);
  };

  return (
    <Stack direction={{xs: "column", md: "row"}} spacing={1}>
      <InputField
        fullWidth
        id="socials"
        onChange={handleChange}
        value={editedWork}
      />
      <Stack direction={{xs: "row"}} spacing={1}>
        <MyDatePicker
          year={editedFromYear}
          setYear={setEditedFromYear}
          views={["year", "month"]}
        />
        <MyDatePicker
          year={editedToYear}
          setYear={setEditedToYear}
          views={["year", "month"]}
        />
        <IconButton sx={{ color: "#000", width: "15%", borderRadius: "5px" }}>
        <ClearIcon fontSize="35px" />
      </IconButton>
      </Stack>
    </Stack>
  );
};

export default WorkItem;
