import { useState } from "react";
import { Stack, IconButton } from "@mui/material";
import ClearIcon from "@mui/icons-material/Clear";

import MyDatePicker from "../MyDatePicker";
import InputField from "../InputField";

const EducationItem = ({ enteredEducation, passingYear }) => {
  const [editedEducation, setEditedEducation] = useState(enteredEducation);
  const [editedPassingYear, setEditedPassingYear] = useState(passingYear);

  const handleChange = (e) => {
    setEditedEducation(e.target.value);
  };

  return (
    <Stack direction="row" spacing={1}>
      <InputField
        sx={{minWidth: "65%"}}
        onChange={handleChange}
        value={editedEducation}
      />
      <MyDatePicker
       sx={{flexBasis: "100%"}}
        year={editedPassingYear}
        setYear={setEditedPassingYear}
        views={["year"]}
      />

      <IconButton sx={{ color: "#000", borderRadius: "5px" }}>
        <ClearIcon fontSize="35px" />
      </IconButton>
    </Stack>
  );
};

export default EducationItem;
