import "./MenuButton.css";

const MenuButton = () => {
    const btnClickHandler = () => {
        document.querySelectorAll(".menu-btn span").forEach((span) => {
          span.classList.toggle("open");
        });
        document.querySelector(".navbar").classList.toggle("open");
        document.querySelector("body").classList.toggle("fixed-position");
    };
      
    return (
        <div className="menu-btn__container" onClick={btnClickHandler}>
        <button
         
          className="menu-btn"
          data-visible="false"
        >
          <span className="rectangle-top rectangle-small"></span>
          <span className="rectangle-middle"></span>
          <span className="rectangle-bottom rectangle-small"></span>
        </button>
        </div>
    )
}

export default MenuButton;