import { useState, useContext } from "react";
import { Box, Stack, IconButton } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import { v4 as uuid } from "uuid";

import GlobalContext from "../../../../context/GlobalContextProvider";
import InputField from "../InputField";
import LabelField from "../LabelField";
import EducationItem from "./EducationItem";
import SubmitButton from "../SubmitButton";
import MyDatePicker from "../MyDatePicker";

const Education = () => {
  const [passingYear, setPassingYear] = useState(null);
  const [enteredEducation, setEnteredEducation] = useState("");
  const [educationList, setEducationList] = useState([]);
  const { istab } = useContext(GlobalContext);

  const handleChange = (e) => {
    setEnteredEducation(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setEducationList((prev) => {
      return [
        ...prev,
        { education: enteredEducation, passingYear: passingYear, id: uuid() },
      ];
    });
    setEnteredEducation("");
    setPassingYear(null);
  };

  return (
    <Box component="form" onSubmit={handleSubmit}>
      <Stack spacing={{xs:1, md: 1.4}}>
      <LabelField htmlFor="education">Education</LabelField>
        <Stack spacing={{xs: 2, md: 3}}>
          {educationList.map((education) => (
            <EducationItem
              setWorkList={setEducationList}
              key={education.id}
              id={education.id}
              enteredEducation={education.education}
              passingYear={education.passingYear}
            />
          ))}
        </Stack>
        <Stack direction="row" alignItems="center" spacing={1}>
            <InputField
              sx={{minWidth: "65%"}}
              onChange={handleChange}
              value={enteredEducation}
              id="education"
              placeholder="St. Marys School"
            />
            <MyDatePicker
              year={passingYear}
              setYear={setPassingYear}
              views={["year"]}
              placeholder="Passing year"
            />
          {istab ? (
            <SubmitButton type="submit">
              <AddIcon fontSize="small" />
              Add
            </SubmitButton>
          ) : (
            <IconButton
              sx={{
                border: "1px solid #000",
                borderRadius: "4px",
                flexGrow: 0,
                padding: ".2em",
              }}
              type="submit"
            >
              <AddIcon sx={{ color: "#000" }} />
            </IconButton>
          )}
        </Stack>
      </Stack>
    </Box>
  );
};

export default Education;
