import { useState, useContext } from "react";
import { Box, Stack, IconButton } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import { v4 as uuid } from "uuid";

import GlobalContext from "../../../../context/GlobalContextProvider";
import InputField from "../InputField";
import LabelField from "../LabelField";
import SubmitButton from "../SubmitButton";
import WorkItem from "./WorkItem";
import MyDatePicker from "../MyDatePicker";

const Work = () => {
  const [fromYear, setFromYear] = useState(null);
  const [toYear, setToYear] = useState(null);
  const [enteredWork, setEnteredWork] = useState("");
  const [workList, setWorkList] = useState([]);
  const { istab } = useContext(GlobalContext);

  const handleChange = (e) => {
    setEnteredWork(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setWorkList((prev) => {
      return [
        ...prev,
        { work: enteredWork, fromYear: fromYear, toYear: toYear, id: uuid() },
      ];
    });
    setEnteredWork("");
    setFromYear(null);
    setToYear(null);
  };

  return (
    <Box component="form" onSubmit={handleSubmit}>
      <Stack spacing={{xs:1, md: 1.4}}>
      <LabelField htmlFor="work-details">Work</LabelField>
        <Stack spacing={{xs: 2, md: 3}}>
          {workList.map((work) => (
            <WorkItem
              setWorkList={setWorkList}
              key={work.id}
              id={work.id}
              enteredWork={work.work}
              fromYear={work.fromYear}
              toYear={work.toYear}
            />
          ))}
        </Stack>
        <Stack direction={{ xs: "column", md: "row" }} spacing={{ xs: 1 }}>
          <InputField
            id="work-details"
            placeholder="Freelancer"
            onChange={handleChange}
            value={enteredWork}
          />
          <Stack
            direction="row"
            spacing={1}
            alignItems="center"
          >
            <Stack direction="row" spacing={1}>
              <MyDatePicker
                year={fromYear}
                setYear={setFromYear}
                views={["year", "month"]}
                placeholder="From"
              />
              <MyDatePicker
                year={toYear}
                setYear={setToYear}
                views={["year", "month"]}
                placeholder="To"
              />
            </Stack>
            {istab ? (
              <SubmitButton type="submit">
                <AddIcon fontSize="small" />
                Add
              </SubmitButton>
            ) : (
              <IconButton
                sx={{ border: "1px solid #000", padding: ".2em", borderRadius: "4px", flexGrow: 0 }}
                type="submit"
              >
                <AddIcon sx={{ color: "#000" }} />
              </IconButton>
            )}
          </Stack>
        </Stack>
      </Stack>
    </Box>
  );
};

export default Work;
